.MenuWrapper {
    margin: 20px auto;
    border: 2px solid white;
    padding: 20px 0;
    width: 900px;
    flex-wrap: wrap;
    display: flex;
    justify-content: space-around;
    align-items: center;
    position: relative;
}

.test{
    position: absolute;
    top: 0;
    left: 0;
    background-image: url(../../data/img/bg-menu.png);
    background-size: 100% 100%;
    background-repeat: no-repeat;
    filter: blur(14px);
    width: 100%;
    height: 100%;
}


